.code-block-page {
    margin: em(20px) 0;
    color: $clr-7;
    width: 100%;

    .btn-back {
        font-size: em(16px);
        width: em(40px);
        height: em(25px);
        border-radius: 5px;
        background-color: $clr-7;
        border: none;
        margin-bottom: em(20px);

        svg {
            color: $clr-1;
        }
    }

    .title {
        font-weight: 600;
        margin-bottom: em(20px, 24px);
    }

    .question {
        font-size: em(20px);
        margin-bottom: em(20px, 20px);
        display: flex;
        flex-direction: column;
        line-height: 1.5;

        span {
            font-weight: 600;
        }
    }

    .actions-container {
        height: em(40px);

        .btn-display {
            font-size: em(16px);
            padding: 0 em(15px);
            background-color: transparent;
            border-radius: 5px 5px 0 0;
            border: none;
            height: 100%;
            color: $clr-7;

            &.active {
                background-color: $clr-7;
                color: $clr-1;
            }
        }

        .btn-container {
            .btn-save, .btn-check {
                height: 90%;
                font-size: em(16px);
                padding: 0 em(15px);
                margin: 0 0 em(5px) 0;
                border-radius: 5px;
                color: $clr-7;
                border: none;
            }
            
            .btn-save{
                margin: 0 0 0 em(10px);
                background-color: $clr-save;
                &:hover{
                    background-color: $clr-save-hover;
                }
            }
            
            .btn-check{
                background-color: $clr-check;
                &:hover{
                    background-color: $clr-check-hover;
                }
            }
        }
    }

    .code-mirror {
        height: 400px;
        max-width: calc(100vw - 20px);
    }

    .gif {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}